<template>
  <div>
    <el-select v-model="value" placeholder="">
      <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>
    <el-input v-model="keyword" placeholder="" style="width:300px"></el-input>
    <el-button type="primary" icon="el-icon-search" @click="onSearch"></el-button>
    <el-table :data="tableData" border style="width: 100%" stripe>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column prop="date" label=""></el-table-column>
      <el-table-column prop="category" label="" style="marginRight:5px"></el-table-column>
      <el-table-column prop="views" label="" width="120"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
          <el-button @click="handleEdit(scope.row)" type="text" size="small"></el-button>
          <el-button @click="handleDelete(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        background
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :total="count"
        :page-size="mount"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
import { GetArticle, DeleteArticle } from "../../api/index";
export default {
  data() {
    return {
      keyword: "",
      data: [], //
      tableData: [],   //
      count: null,   //
      mount: 7,    //
      currentpage: 1,
      options: [
        {
          value: "category",
          label: ""
        },
        {
          value: "title",
          label: ""
        }
      ],
      value: "title"
    };
  },
  created() {
    this.getDate({});
  },
  methods: {
    // 
    handleClick(row) {
      this.$router.push({ name: "article", query: { id: row._id } });
    },
    // 
    handleEdit(row) {
      this.$emit("edit", row);
    },
    // 
    handleDelete(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          DeleteArticle({ _id: row._id }).then(res => {
            if (res.status) {
              this.$notify({
                title: "",
                message: res.msg,
                type: "success"
              });
              this.getDate({});
            } else {
              this.$notify.error({
                title: "",
                message: res.msg
              });
            }
          });
        })
        .catch(() => {
          this.$notify.info({
            title: "",
            message: ""
          });
        });
    },

    // 
    handleCurrentChange(val) {
      this.currentpage = val;
      this.currentData();
    },

    // 
    onSearch() {
      const data = this.keyword == "" ? {} : { [this.value]: this.keyword };
      this.getDate(data);
    },

    //
    getDate(data) {
      GetArticle(data).then(res => {
        this.data = [];
        this.data.push(...res.data);
        this.count = res.count;
        this.currentData();
      });
    },

    // 
    currentData() {
      this.tableData = this.data.filter((item, index) => {
        return (
          index < this.mount * this.currentpage &&
          index >= this.mount * (this.currentpage - 1)
        );
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.pagination {
  margin: 10px 0px;
  text-align: right;
}

.el-input {
  margin: 0px 20px;
  margin-bottom: 20px;
}
.el-select {
  width: 150px;
}
</style>