<template>
  <div>
    <el-input v-model="content" type="textarea" placeholder="" style="width:300px"></el-input>
    <el-button class="fabutton" type="primary" icon="el-icon-position" @click="release"></el-button>
    <el-input
      class="searchInput"
      placeholder=""
      maxlength="10"
      show-word-limit
      prefix-icon="el-icon-search"
      v-model="serachName"
      clearable
    ></el-input>
    <el-button type="primary" icon="el-icon-search" @click="serachmessage"></el-button>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="date" label="" width="200px"></el-table-column>
      <el-table-column prop="name" label="" width="150px"></el-table-column>
      <el-table-column prop="content" label=""></el-table-column>
      <el-table-column label="" width="100px">
        <template slot-scope="scope">
          <el-button type="text" size="small"></el-button>
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="page">
      <el-pagination
        background
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :page-size="pagesize"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
import { PostMessage, GetMessage, DeleteMessage } from "../../api/index";
export default {
  data() {
    return {
      pagesize: 7,
      total: 0,
      content: "",
      currentPage: 1,
      tableData: [],
      serachName:""
    };
  },
  created() {
    this.getMessage({ page: this.currentPage, limit: this.pagesize });
  },
  methods: {
    handleClick(row) {
      console.log(row);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          DeleteMessage({ _id: row._id }).then(res => {
            if (res.status) {
              this.$notify({
                title: "",
                message: res.msg,
                type: "success"
              });
              this.getMessage({ page: this.currentPage, limit: this.pagesize });
            } else {
              this.$notify.error({
                title: "",
                message: res.msg
              });
            }
          });
        })
        .catch(() => {
          this.$notify.info({
            title: "",
            message: ""
          });
        });
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getMessage({name:this.serachName, page: this.currentPage, limit: this.pagesize });
    },
    // 
    getMessage(data) {
      GetMessage(data).then(res => {
        this.tableData = [];
        const { data, status, msg, count } = res;
        if (status) {
          this.tableData.push(...data);
          this.total = count;
        }
      });
    },
    // 
    release() {
      PostMessage({ name: "", content: this.content, isAdmin: true }).then(
        res => {
          if (res.status) {
            this.getMessage({ page: 1, limit: this.pagesize });
            this.content = "";
            this.$notify({
              title: "",
              message: res.msg,
              type: "success"
            });
          } else {
            this.$notify.error({
              title: "",
              message: res.msg
            });
          }
        }
      );
    },
    serachmessage() {
      this.getMessage({ name: this.serachName,page:1,limit:this.pagesize});
    }
  }
};
</script>
<style lang="scss" scoped>
.el-textarea {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
}
.fabutton {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
}
.page {
  margin: 10px 0px;
  text-align: right;
}
.searchInput{
  width: 270px;
  margin-right: 20px;
}
</style>